.footer-content {
  margin-bottom: 16px;
  @media (min-width: @tablet-width) {
    .flex();
    justify-content: space-between;
  }
}

.footer {
  background-color: #262626;

  &__logo {
    width: calc(100vw - 8px);
    padding-top: 56px;
    padding-bottom: 40px;
    border-bottom: 1px solid fade(@white, 40%);
    @media (min-width: @tablet-width) {
      width: calc(100vw - 16px);
    }
    @media (min-width: @desktop-width) {
      width: calc(@desktop-width - 100px);
      a {
        .flex();
        margin-left: -50px;
        width: max-content;
      }
    }
  }

  &__aside {
    @media (min-width: @tablet-width) {
      width: 23.406%;
    }
  }

  &__language {
    position: relative;
    .flex();
    justify-content: space-between;
    margin-top: 32px;
    padding: 12px 0;
    background: #3b3b3b;
    border-radius: 2px;
    width: 53.4%;
    @media (min-width: @tablet-width) {
      width: 100%;
    }
  }

  &__language-image {
    width: 16px;
    overflow: hidden;
    position: relative;
    height: 16px;
    margin-left: 25px;

    img {
      width: 16px;
      position: absolute;
      height: 16px;
      top: 0;
      left: -7px;
    }
  }

  .button-image {
    width: 20px;
    height: 20px;
    position: absolute;
    left: 50px;
    top: 10px;
  }

  &__language-button {
    position: relative;
    text-align: left;
    width: 65%;
    font-size: 13px;
    line-height: 15px;
    color: @white;
    background-color: transparent;
    transition: all 0.35s ease-in-out;

    &:hover,
    &:focus {
      &::before,
      &::after {
        background-color: #4cc89e;
      }

      &::before {
        width: 20px;
        height: 6px;
      }

      &::after {
        width: 15px;
        height: 6px;
        right: 32px;
      }
    }

    &::before {
      content: "";
      width: 14px;
      height: 3px;
      background-color: #fff;
      transform: rotate(-45deg);
      right: 20px;
      top: 6px;
      z-index: 10;
      transition: all 0.35s ease-in-out;
    }

    &::after {
      content: "";
      width: 8px;
      height: 3px;
      background-color: white;
      transform: rotate(45deg);
      right: 30px;
      top: 6px;
      transition: all 0.35s ease-in-out;
    }
  }

  &__hashtags {
    margin-top: 32px;
    color: @white;
    font-size: 16px;
    line-height: 19px;
    @media (min-width: @tablet-width) {
    }

    span {
      color: #49a144;
    }
  }

  &__social {
    .flex();
    justify-content: space-between;
    width: 29.63%;
    margin-top: 16px;
    @media (min-width: @tablet-width) {
      width: 55.57%;
    }
  }

  &__links {
    overflow: hidden;
    transition: all 0.3s ease-in-out;

    svg {
      width: 24px;
      height: 24px;
      fill: @white;
      transition: all 0.3s ease-in-out;
    }

    &:hover,
    &:focus {
      svg {
        width: 30px;
        height: 30px;
        fill: #4cc89e;
      }
    }
  }

  &__app-download {
    .flex();
    justify-content: space-between;
    padding: 24px 16px 24px 31px;
    width: 100%;
    border-top: 1px solid fade(@white, 40%);
    border-bottom: 1px solid fade(@white, 40%);
    @media (min-width: @tablet-width) {
      padding: 24px 230px;
    }
  }

  &__app-image {
    &--appstore {
      width: 109px;
      height: 36px;
      @media (min-width: @tablet-width) {
        width: 120px;
        height: 40px;
      }
    }

    &--google {
      width: 124px;
      height: 36px;
      @media (min-width: @tablet-width) {
        width: 135px;
        height: 40px;
      }
    }
  }

  &__rights {
    .flex();
    flex-direction: column;
    margin-top: 16px;
    padding-bottom: 24px;
    position: relative;
    width: 100%;
    @media (min-width: @tablet-width) {
      flex-direction: row;
    }
  }

  &__rights-text {
    margin-top: 24px;
    font-size: 11px;
    line-height: 13px;
    color: @white;
    @media (min-width: @tablet-width) {
      width: 33%;
    }
  }

  &__rights-button {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 53px;
    height: 64px;
    background-color: #59bd5a;
    border-radius: 2px;
    transition: all 0.2s ease-in-out;

    &:hover,
    &:focus {
      border: 1px solid @white;

      &::before {
        transform: translateY(-15px);
      }
    }

    @media (min-width: @tablet-width) {
      width: 80px;
      height: 80px;
    }

    &::before {
      content: "";
      width: 12.65px;
      height: 24.99px;
      top: 20px;
      left: 20px;
      background-image: url("../img/icons/arrow.svg");
      transition: all 0.35s ease-in-out;
      @media (min-width: @tablet-width) {
        top: 30px;
        left: 35px;
      }
    }
  }
}

.site-menu {
  .flex();
  flex-direction: column;
  width: 100%;
  @media (min-width: @tablet-width) {
    width: 26%;
  }
  @media (min-width: @desktop-width) {
    width: 20%;
  }

  &__item {
    margin-top: 23px;
    position: relative;

    a {
      font-size: 13px;
      line-height: 15px;
      color: @white;
      transition: color 0.25s ease-in-out;
      width: max-content;
      position: relative;

      &::before {
        display: block;
        content: "";
        background-color: #4cc89e;
        height: 2px;
        width: 0%;
        left: 0;
        bottom: -8px;
        transition: width 0.5s ease-in-out;
        transform: translateX(0%);
        z-index: 100;
      }

      &:hover,
      &:focus {
        color: #f97676;

        &::before {
          width: 100%;
        }
      }
    }
  }
}

.second-menu {
  padding-bottom: 40px;
  @media (min-width: @desktop-width) {
    width: 32%;
  }
}
