.wrapper {
  box-sizing: border-box;
  margin: 0 auto;
  max-width: 1124px;
  padding-left: 4px;
  padding-right: 4px;
  @media screen and (min-width: @tablet-width) {
    padding-left: 8px;
    padding-right: 8px;
  }
  @media screen and (min-width: @desktop-width) {
    padding-right: 50px;
    padding-left: 50px;
  }
}
