* {
  padding: 0;
  margin: 0;
  border: 0;
}
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
*:before,
*:after {
  position: absolute;
}
:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}
nav,
footer,
header,
aside {
  display: block;
}
html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
  font-family: inherit;
}
input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}
.wrapper {
  box-sizing: border-box;
  margin: 0 auto;
  max-width: 1124px;
  padding-left: 4px;
  padding-right: 4px;
}
@media screen and (min-width: 768px) {
  .wrapper {
    padding-left: 8px;
    padding-right: 8px;
  }
}
@media screen and (min-width: 1124px) {
  .wrapper {
    padding-right: 50px;
    padding-left: 50px;
  }
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
}
.navigation {
  min-height: 72px;
  padding-top: 16px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
.navigation__logo-container {
  flex-grow: 1;
}
.navigation__logo {
  cursor: pointer;
  width: 107px;
}
.navigation__logo img {
  transition: all 0.25s ease-in;
}
.navigation__logo:hover img,
.navigation__logo:focus img {
  transform: translateY(-10px);
  width: 120px;
  height: 25px;
}
@media (min-width: 1124px) {
  .navigation__logo {
    width: 109px;
  }
}
.navigation__login {
  align-self: flex-start;
  padding: 7.2px 20px;
  background-color: transparent;
  border: 1px solid #e0e0e0;
  border-radius: 2px;
  font-weight: 500;
  font-size: 13px;
  line-height: 24px;
  transition: box-shadow 0.35s ease-in-out, color 0.25s ease-in-out;
  width: 78px;
}
.navigation__login:hover,
.navigation__login:focus {
  color: #ffff;
  box-shadow: 0 0 40px 40px #59bd5a inset;
}
.navigation__basket {
  display: none;
  width: 19px;
  height: 40px;
  margin-right: 10px;
  margin-left: 20px;
}
.navigation__basket:hover svg,
.navigation__basket:focus svg {
  width: 25px;
  height: 30px;
}
.navigation__basket svg {
  width: 19px;
  height: 22px;
}
@media (min-width: 630px) {
  .navigation__basket {
    display: block;
  }
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0;
  min-height: 100%;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #1d1d1d;
  background-color: #ffff;
  -webkit-tap-highlight-color: transparent;
  overflow-x: hidden;
}
.page-header {
  box-shadow: 0 0 16px rgba(38, 38, 38, 0.16);
}
.form {
  width: 100%;
  margin-top: 32px;
}
.form__label {
  display: block;
  min-height: 48px;
  position: relative;
}
.form__label::before {
  display: block;
  content: "";
  background-color: #59bd5a;
  height: 2px;
  width: 0%;
  left: 50%;
  bottom: 0;
  transition: width 0.45s ease-in-out;
  transform: translateX(-50%);
  z-index: 100;
}
.form__label:hover .form__input,
.form__label:focus .form__input {
  border: none;
}
.form__label:hover::before,
.form__label:focus::before {
  width: 100%;
}
.form__input {
  font-size: 16px;
  line-height: 24px;
  color: rgba(98, 98, 98, 0.5);
  position: relative;
  width: 100%;
  height: 48px;
  border-bottom: 1px solid rgba(117, 117, 117, 0.3);
}
.form__input::placeholder {
  font-size: 16px;
  line-height: 24px;
  color: rgba(98, 98, 98, 0.5);
}
.shops {
  margin-top: 56px;
  min-height: max-content;
}
.shops__title {
  color: #1d1d1d;
  font-size: 22px;
  line-height: 34px;
}
.shop-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 16px;
}
@media screen and (min-width: 630px) {
  .shop-list {
    justify-content: space-between;
    margin-bottom: 24px;
  }
}
@media screen and (min-width: 1124px) {
  .shop-list {
    justify-content: center;
    margin-bottom: 64px;
  }
}
.shop-list__item {
  min-height: 356px;
  margin-bottom: 16px;
  min-width: 308px;
  width: calc(100vw - 35vw);
  overflow: hidden;
  transition: background-color 0.25s ease-in-out;
}
.shop-list__item:hover,
.shop-list__item:focus {
  background-color: #f2f2f2;
  border: 3px solid #eaeaea;
  border-radius: 6px;
  margin-bottom: 13px;
}
.shop-list__item:nth-child(1n + 5) {
  display: none;
}
@media screen and (min-width: 768px) {
  .shop-list__item:nth-child(1n + 5) {
    display: block;
  }
}
@media (min-width: 630px) {
  .shop-list__item {
    width: 48.937%;
  }
}
@media (min-width: 900px) {
  .shop-list__item {
    width: 32.6%;
    min-width: 250px;
  }
}
@media screen and (min-width: 1124px) {
  .shop-list__item {
    width: 32%;
    min-width: 308px;
  }
  .shop-list__item:nth-child(3n + 2) {
    margin-right: 16px;
    margin-left: 16px;
  }
}
.shop-list__image {
  width: 100%;
  height: 246px;
  object-fit: cover;
}
@media screen and (min-width: 768px) {
  .shop-list__image {
    height: 256px;
  }
}
.shop-list__name {
  margin-top: 16px;
  color: #1d1d1d;
  font-size: 16px;
  line-height: 24px;
}
.shop-list__type {
  color: #626262;
  font-size: 14px;
  line-height: 21px;
  margin-top: 4px;
  margin-bottom: 4px;
}
.shop-list__time {
  color: #1d1d1d;
  font-size: 13px;
  line-height: 21px;
}
.footer-content {
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .footer-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
.footer {
  background-color: #262626;
}
.footer__logo {
  width: calc(100vw - 8px);
  padding-top: 56px;
  padding-bottom: 40px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
}
@media (min-width: 768px) {
  .footer__logo {
    width: calc(100vw - 16px);
  }
}
@media (min-width: 1124px) {
  .footer__logo {
    width: calc(1124px - 100px);
  }
  .footer__logo a {
    display: flex;
    flex-wrap: wrap;
    margin-left: -50px;
    width: max-content;
  }
}
@media (min-width: 768px) {
  .footer__aside {
    width: 23.406%;
  }
}
.footer__language {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 32px;
  padding: 12px 0;
  background: #3b3b3b;
  border-radius: 2px;
  width: 53.4%;
}
@media (min-width: 768px) {
  .footer__language {
    width: 100%;
  }
}
.footer__language-image {
  width: 16px;
  overflow: hidden;
  position: relative;
  height: 16px;
  margin-left: 25px;
}
.footer__language-image img {
  width: 16px;
  position: absolute;
  height: 16px;
  top: 0;
  left: -7px;
}
.footer .button-image {
  width: 20px;
  height: 20px;
  position: absolute;
  left: 50px;
  top: 10px;
}
.footer__language-button {
  position: relative;
  text-align: left;
  width: 65%;
  font-size: 13px;
  line-height: 15px;
  color: #ffff;
  background-color: transparent;
  transition: all 0.35s ease-in-out;
}
.footer__language-button:hover::before,
.footer__language-button:focus::before,
.footer__language-button:hover::after,
.footer__language-button:focus::after {
  background-color: #4cc89e;
}
.footer__language-button:hover::before,
.footer__language-button:focus::before {
  width: 20px;
  height: 6px;
}
.footer__language-button:hover::after,
.footer__language-button:focus::after {
  width: 15px;
  height: 6px;
  right: 32px;
}
.footer__language-button::before {
  content: "";
  width: 14px;
  height: 3px;
  background-color: #fff;
  transform: rotate(-45deg);
  right: 20px;
  top: 6px;
  z-index: 10;
  transition: all 0.35s ease-in-out;
}
.footer__language-button::after {
  content: "";
  width: 8px;
  height: 3px;
  background-color: white;
  transform: rotate(45deg);
  right: 30px;
  top: 6px;
  transition: all 0.35s ease-in-out;
}
.footer__hashtags {
  margin-top: 32px;
  color: #ffff;
  font-size: 16px;
  line-height: 19px;
}
.footer__hashtags span {
  color: #49a144;
}
.footer__social {
  display: flex;
