  margin-top: 16px;
}
@media (min-width: 768px) {
  .footer__social {
    width: 55.57%;
  }
}
.footer__links {
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}
.footer__links svg {
  width: 24px;
  height: 24px;
  fill: #ffff;
  transition: all 0.3s ease-in-out;
}
.footer__links:hover svg,
.footer__links:focus svg {
  width: 30px;
  height: 30px;
