}
.footer__rights-button:hover::before,
.footer__rights-button:focus::before {
  transform: translateY(-15px);
}
@media (min-width: 768px) {
  .footer__rights-button {
    width: 80px;
    height: 80px;
  }
}
.footer__rights-button::before {
  content: "";
  width: 12.65px;
  height: 24.99px;
  top: 20px;
  left: 20px;
  background-image: url("../img/icons/arrow.svg");
  transition: all 0.35s ease-in-out;
}
@media (min-width: 768px) {
  .footer__rights-button::before {
    top: 30px;
    left: 35px;
  }
}
.site-menu {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
}
@media (min-width: 768px) {
