.navigation {
  min-height: 72px;
  padding-top: 16px;
  .flex();
  align-items: center;
  justify-content: space-between;

  &__logo-container {
    flex-grow: 1;
  }

  &__logo {
    cursor: pointer;
    width: 107px;

    img {
      transition: all 0.25s ease-in;
    }

    &:hover,
    &:focus {
      img {
        transform: translateY(-10px);
        width: 120px;
        height: 25px;
      }
    }

    @media (min-width: @desktop-width) {
      width: 109px;
    }
  }

  &__login {
    align-self: flex-start;
    padding: 7.2px 20px;
    background-color: transparent;
    border: 1px solid #e0e0e0;
    border-radius: 2px;
    font-weight: @font-weight-medium;
    font-size: 13px;
    line-height: 24px;
    transition: box-shadow 0.35s ease-in-out, color 0.25s ease-in-out;
    width: 78px;

    &:hover,
    &:focus {
      color: @white;
      box-shadow: 0 0 40px 40px #59bd5a inset;
    }
  }

  &__basket {
    display: none;
    width: 19px;
    height: 40px;
    margin-right: 10px;
    margin-left: 20px;

    &:hover,
    &:focus {
      svg {
        width: 25px;
        height: 30px;
      }
    }

    svg {
      width: 19px;
      height: 22px;
    }

    @media (min-width: 630px) {
      display: block;
    }
  }
}
