.form {
  width: 100%;
  margin-top: 32px;

  &__label {
    display: block;
    min-height: 48px;
    position: relative;

    &::before {
      display: block;
      content: "";
      background-color: #59bd5a;
      height: 2px;
      width: 0%;
      left: 50%;
      bottom: 0;
      transition: width 0.45s ease-in-out;
      transform: translateX(-50%);
      z-index: 100;
    }

    &:hover,
    &:focus {
      .form__input {
        border: none;
      }

      &::before {
        width: 100%;
      }
    }
  }

  &__input {
    font-size: 16px;
    line-height: 24px;
    color: fade(#626262, 50%);
    position: relative;
    width: 100%;
    height: 48px;
    border-bottom: 1px solid fade(#757575, 30%);

    &::placeholder {
      font-size: 16px;
      line-height: 24px;
      color: fade(#626262, 50%);
    }
  }
}
