.restaurants {
  position: relative;
  width: 100%;
  min-height: 255px;
  background-image: url("../img/pushkin.png");
  background-size: cover;
  background-repeat: no-repeat;
  @media (min-width: @tablet-width) {
    min-height: 364px;
  }

  &__title {
    margin-bottom: 40px;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    @media (min-width: @tablet-width) {
      font-size: 36px;
      line-height: 42px;
    }
  }

  &__type {
    margin-bottom: 16px;
    font-size: 14px;
    line-height: 12px;
    color: #626262;
    @media (min-width: @tablet-width) {
      width: 60%;
      margin-bottom: 0;
      height: 50px;
    }
  }

  &__time {
    color: @white;
    font-size: 12px;
    line-height: 16px;
    width: 98px;
    text-align: center;
    padding: 8px 0;
    background: #262626;
    border-radius: 16px;
  }
}

.restaurant-inner {
  position: relative;
  height: 255px;
  @media (min-width: @tablet-width) {
    height: 364px;
  }
}

.restaurant-container {
  position: absolute;
  top: 60%;
  left: 8px;
  .flex();
  flex-direction: column;
  align-items: center;
  padding: 24px 49px;
  background-color: @white;
  box-shadow: 0px 0px 16px rgba(117, 117, 117, 0.2);
  border-radius: 2px;

  @media (min-width: 630px) {
    flex-direction: row;
    align-items: baseline;
    padding-top: 38px;
    top: 25%;
    width: 432px;
  }
  @media (min-width: 900px) {
    padding: 38px 38px 10px;
    width: 389px;
    left: 50px;
  }
}
