.dishes {
  &__list {
    display: none;
    @media (min-width: @tablet-width) {
      .flex();
      position: relative;
      &::before {
        content: "";
        bottom: 0;
        left: -8px;
        width: 100vw;
        height: 1px;
        background-color: rgba(117, 117, 117, 0.2);
        @media (min-width: @desktop-width) {
          width: 1124px;
          left: -50px;
        }
      }
    }
  }

  &__items {
    padding: 30px 0;
    width: max-content;
    padding-right: 40px;
  }

  &__links {
    color: #626262;
    font-size: 16px;
    line-height: 19px;
    transition: color 0.35s ease-in-out;

    &:hover,
    &:focus {
      color: #4cc89e;
    }
  }

  &__title {
    margin-top: 112px;
    margin-bottom: 56px;
    position: relative;
    color: @basic-text;
    font-size: 22px;
    line-height: 34px;
    @media (min-width: @tablet-width) {
      margin-top: 56px;
      margin-bottom: 32px;
    }
  }
}

.dishes-cards {
  .flex();
  margin-bottom: 24px;
  @media (min-width: @tablet-width) {
    margin-bottom: 16px;
    justify-content: center;
  }
  @media (min-width: @desktop-width) {
    margin-bottom: 31px;
    justify-content: space-between;
  }

  &__item {
    cursor: pointer;
    margin-bottom: 16px;
    .flex();
    justify-content: flex-start;
    width: 100%;
    border-bottom: 1px solid fade(#757575, 20%);
    transition: background-color 0.25s ease-in;

    &:hover,
    &:focus {
      background-color: fade(#757575, 4%);
    }

    @media (min-width: 530px) {
      border: 1px solid fade(#757575, 20%);
      margin-bottom: 32px;
      border-radius: 3px;
      padding-left: 24px;
      max-width: 752px;
    }
    @media (min-width: 1000px) {
      margin-bottom: 49px;
      flex-basis: 49%;
      padding-left: 16px;
    }

    img {
      order: 1;
      width: 102px;
      height: 102px;
      border-radius: 3px;
      @media (min-width: 530px) {
        width: 200px;
        height: 160px;
      }
    }
  }

  &__description {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    flex-grow: 1;
    justify-content: space-between;
  }

  &__name {
    font-size: 14px;
    line-height: 24px;
    @media (min-width: 530px) {
      padding-top: 16px;
      font-size: 16px;
      line-height: 24px;
    }
  }

  &__ingredients {
    color: #626262;
    font-size: 13px;
    line-height: 21px;
    min-height: 21px;
    height: 40px;
  }

  &__price {
    height: 32px;
    font-weight: @font-weight-medium;
    font-size: 15px;
    line-height: 21px;
    @media (min-width: 530px) {
      margin-top: 42px;
    }
  }
}
