.shops {
  margin-top: 56px;
  min-height: max-content;

  &__title {
    color: @basic-text;
    font-size: 22px;
    line-height: 34px;
  }
}

.shop-list {
  .flex();
  justify-content: center;
  margin-top: 16px;
  @media screen and (min-width: 630px) {
    justify-content: space-between;
    margin-bottom: 24px;
  }
  @media screen and (min-width: @desktop-width) {
    justify-content: center;
    margin-bottom: 64px;
  }

  &__item {
    min-height: 356px;
    margin-bottom: 16px;
    min-width: 308px;
    width: calc(100vw - 35vw);
    overflow: hidden;
    transition: background-color 0.25s ease-in-out;

    &:hover,
    &:focus {
      background-color: #f2f2f2;
      border: 3px solid #eaeaea;
      border-radius: 6px;
      margin-bottom: 13px;
    }

    &:nth-child(1n + 5) {
      display: none;
      @media screen and (min-width: @tablet-width) {
        display: block;
      }
    }

    @media (min-width: 630px) {
      width: 48.937%;
    }

    @media (min-width: 900px) {
      width: 32.6%;
      min-width: 250px;
    }
    @media screen and (min-width: @desktop-width) {
      width: 32%;
      min-width: 308px;
      &:nth-child(3n + 2) {
        margin-right: 16px;
        margin-left: 16px;
      }
    }
  }

  &__image {
    width: 100%;
    height: 246px;
    object-fit: cover;
    @media screen and (min-width: @tablet-width) {
      height: 256px;
    }
  }

  &__name {
    margin-top: 16px;
    color: @basic-text;
    font-size: 16px;
    line-height: 24px;
  }

  &__type {
    color: #626262;
    font-size: 14px;
    line-height: 21px;
    margin-top: 4px;
    margin-bottom: 4px;
  }

  &__time {
    color: @basic-text;
    font-size: 13px;
    line-height: 21px;
  }
}
